/* DISPLAY UTILITY STYLES
   ----------------------------- */
.block {
  display: block !important; }

.inline-block {
  display: inline-block !important; }

.inline {
  display: inline !important; }

.display-flex {
  display: flex; }

/* TYPOGRAPHY UTILITY STYLES
   ----------------------------- */
.truncate {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.line-through {
  text-decoration: line-through; }

.bold {
  font-weight: bold; }

.all-caps {
  text-transform: uppercase; }

.hint-text {
  opacity: .7; }

.text-ellipsis {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

/* TEXT COLOR STYLES
   ----------------------------- */
.text-inherit {
  color: inherit; }

.text-black {
  color: black; }

.text-white {
  color: white; }

/* IMAGE UTILITY STYLES
   ----------------------------- */
img.img-circle {
  border-radius: 50%; }

/* CURSOR STYLES
   ----------------------------- */
.cursor-pointer {
  cursor: pointer; }

/* BACKGROUND UTILITY STYLES
   ----------------------------- */
.bg-white {
  background: #fff !important; }

/* BODER UTILITY STYLES
   ----------------------------- */
.no-border {
  border-color: transparent;
  border-width: 0; }

.no-borders {
  border-width: 0 !important; }

.b {
  border-color: #d5dce6;
  background-clip: padding-box; }

.b-a {
  border: 1px solid #d5dce6; }

.b-t {
  border-top: 1px solid #d5dce6; }

.b-r {
  border-right: 1px solid #d5dce6; }

.b-b {
  border-bottom: 1px solid #d5dce6; }

.b-l {
  border-left: 1px solid #d5dce6; }

/* BORDER RADIUS STYLES
   ----------------------------- */
.b-radius {
  border-radius: 4px;
  overflow: hidden; }

.b-circle {
  border-radius: 500px;
  overflow: hidden; }
